import React, { useState } from 'react';
import{dispatch, useDispatch} from 'react-redux';
import { ProfileUpdateAction } from '../../../Action/userAction';

export default function ProfileEdit() {
const dispatch=useDispatch()

  const [number, setNumber] = useState('');
  const [email, setEmail] = useState('');
  const [name, setName] = useState('');
  const [otp, setOtp] = useState('');

  const handleNumber = async (e) => {
    e.preventDefault();
    const updateData={ContactNumber:number,email:email,name:name,Otp:otp};
    dispatch(ProfileUpdateAction(updateData))
  }

  // Inline styles
  const styles = {
    body: {
      fontFamily: 'Arial, sans-serif',
      height: '45vh',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: '#f0f0f0',
    },
    
    formContainer: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      height: '45vh',
      padding: '10px',
      marginTop: '25px',
      marginBottom: '25px',
    },
    form: {
      backgroundColor: 'white',
      padding: '30px',
      borderRadius: '8px',
      boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
      display: 'flex',
      flexDirection: 'column',
      gap: '15px',
      width: '100%',
      maxWidth: '400px',
    },
    input: {
      padding: '10px',
      fontSize: '16px',
      margin: '5px 0',
      border: '1px solid #ccc',
      borderRadius: '5px',
    },
    inputFocus: {
      borderColor: '#4CAF50',
      outline: 'none',
    },
    button: {
      padding: '10px',
      fontSize: '16px',
      backgroundColor: '(--main-light-clr);',
      color: 'white',
      border: 'none',
      borderRadius: '5px',
      cursor: 'pointer',
      transition: 'background-color 0.3s',
    },
    buttonHover: {
      backgroundColor: '#45a049',
    },
  };

  return (
    <div style={styles.formContainer}>
      {/* <div>fhe</div> */}
      <div>
      <form onSubmit={handleNumber} style={styles.form}>
        <p>Fill The box you want to change !</p>
        <input
          type="number"
          name="ContactNumber"
          placeholder="Type New number"
          value={number}
          onChange={(e) => setNumber(e.target.value)}
          style={styles.input}
        />
        <input
          type="email"
          name="email"
          placeholder="Type email here"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          style={styles.input}
        />
        <input
          type="text"
          name="Name"
          placeholder="Type name here"
          value={name}
          onChange={(e) => setName(e.target.value)}
          style={styles.input}
        />
        <input
          type="number"
          name="Otp"
          placeholder="Enter OTP here"
          value={otp}
          onChange={(e) => setOtp(e.target.value)}
          style={styles.input}
        />
        <button type="submit" style={styles.button}>Submit</button>
      </form>
      </div>
    </div>
  );
}
