import React, { useState,useEffect } from "react";
import { Link } from "react-router-dom";
import "./ProfileHeader.css";
import { useSelector } from "react-redux";
import { GetPost_BiddingDocumentAction } from "../../../Action/userAction";
function ProfileHeader() {
  const { medata } = useSelector((state) => {
    return state.meDetails;
  });

  // useEffect(()=>{

  // } ,[medata])
  const styles = {
    bodybutton: {
      display: 'flex',
      justifyContent: 'flex-end', // Aligns items to the right
      alignItems: 'center',
      width: '100%', // Ensures the container takes up the full width
      padding: '10px', // Adds some spacing inside the container
    },
  
    b1: {
      display: "flex",
      padding: "5px",
      border: "1px solid var(--main-light-clr)",  // Green border
      borderRadius: "8px",  // Adds rounded corners (adjust the value as needed)
    },
  
    text1: {
      padding: "4px",
    },
  
    // Toggle switch styles
    toggleSwitchContainer: {
      position: 'relative',
      display: 'inline-block',
      width: '50px',
      height: '24px',
    },
  
    toggleSwitchInput: {
      opacity: 0,
      width: 0,
      height: 0,
    },
  
    slider: {
      position: 'absolute',
      cursor: 'pointer',
      backgroundColor: '#ccc',
      borderRadius: '24px',
      width: '100%',
      height: '100%',
      transition: 'background-color 0.3s',
    },
  
    sliderBefore: {
      content: '""',
      position: 'absolute',
      height: '20px',
      width: '20px',
      left: '4px',
      bottom: '2px',
      backgroundColor: 'white',
      borderRadius: '50%',
      transition: 'transform 0.3s',
    },
  
    // Toggled state when input is checked
    toggleChecked: {
      backgroundColor: '#4caf50',
    },
  
    toggleCheckedBefore: {
      transform: 'translateX(26px)',
    },
  };
  
 
  
  
  const [isToggled, setIsToggled] = useState(false);

  // Toggle the state when the button is clicked
  const handleToggleChange = () => {
    setIsToggled(!isToggled);
  };

  return (
    <>
      <div className="profile-main-box">
        {medata && medata.IsAuthenticated == true && (
          <header>
            <div className="userDataSection">
              <div>
                <p className="profile-name-que">Name </p>
                <p className="profile-name-ans ans">{medata.user.Name}</p>
              </div>

              <div>
                <p className="profile-regis-que">Registerd As</p>
                <p className="profile-regis-ans ans">{medata.user.Role}</p>
              </div>

              <div>
                <p className="Phoneno-que">Mobile No</p>
                <p className="profile-name-ans ans">{medata.user.ContactNumber}</p>
                <Link to="/user/profileEdit" style={{display:"flex"}}>
             <span style={{fontSize:"11px",marginBottom:"10px"}}>Click here for Change your Contact</span>
              </Link>
              </div>
              <div>
                <p className="email-que"> Email </p>
                <p className="profile-name-ans ans">{medata.user.email}</p>
              </div>
              {/* <button className="editProfileBtn">Edit Profile</button> */}
            </div>
            <div className="whatapp-notify ">
              <img
                className="icons"
                src="/img/whatapp.png"
                alt="whatapp logo"
              />
              <p>Receive updates on whatsapp</p>
              <div style={{ padding: "5px" }}>
              {/* <button  style={{ padding: "5px", borderRadius: "10px" }}  onClick={handleToggle}>
                   {isToggled ? 'whatsApp status Show' : 'whatsApp status hide'} 
              </button> */}

            <label style={styles.toggleSwitchContainer}>
          <input 
            type="checkbox"
            checked={isToggled} 
            onChange={handleToggleChange} 
            style={styles.toggleSwitchInput} 
          />
          <span 
            style={{
              ...styles.slider,
              ...(isToggled ? styles.toggleChecked : {}),
            }}
          >
            <span
              style={{
                ...styles.sliderBefore,
                ...(isToggled ? styles.toggleCheckedBefore : {}),
              }}
            />
          </span>
           </label>
    </div>
   
            </div>
            {/* toggle button for hide the whatsapp  */}

             

            <div style={styles.bodybutton}>
            <Link to="/user/profileEdit" style={styles.b1}>
    <span style={styles.text1}>Edit Profile</span>
  </Link>
 
</div>

          </header>
        )}
      </div>
    </>
  );
}

export default ProfileHeader;
